/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  background-color: black; }
  @media only screen and (max-width: 68.75em) {
    html {
      font-size: 50%; } }
  @media only screen and (max-width: 56.25em) {
    html {
      font-size: 40%; } }
  @media only screen and (max-width: 31.25em) {
    html {
      font-size: 30%; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  box-sizing: border-box; }

.layout {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("/images/background.webp");
  padding-bottom: 5rem; }

@media only screen and (max-width: 56.25em) {
  .page_container {
    margin-top: 20rem; } }

.bck_b_dark {
  background: #272723; }

.dialog_alert {
  padding: 5.1rem;
  font-size: 3rem; }

.no_result {
  font-size: 4rem;
  color: #999592;
  text-align: center;
  padding-top: 4rem; }

.not_found_container {
  font-size: 7rem;
  text-align: center;
  padding-top: 7rem;
  color: #dddddd; }

.clear:after {
  content: '';
  display: block;
  clear: both; }

.footer_container {
  display: flex;
  justify-content: center;
  align-content: center;
  margin-top: 2rem;
  margin-bottom: 5rem;
  background-color: transparent; }

.footer_right {
  min-width: 10rem !important;
  height: 4rem;
  line-height: 4rem; }

.footer_center {
  background-color: #e7eb05;
  height: 4rem;
  width: 40rem;
  text-align: center;
  line-height: 4rem;
  color: #2b2b2b;
  border-radius: 5rem;
  font-size: 1.2rem; }
  .footer_center:hover {
    cursor: pointer;
    text-decoration: none;
    background-color: #39d3db;
    border: solid #39d3db 0px; }

.footer_right {
  min-width: 25%;
  height: 4rem;
  display: flex;
  justify-content: flex-end;
  align-items: center; }
  .footer_right a:hover {
    text-decoration: none !important; }
  .footer_right .bottom_button {
    width: 4rem;
    height: 4rem;
    font-size: 2rem;
    background-color: #e7eb05;
    text-align: center;
    color: #2b2b2b;
    line-height: 4rem;
    border-radius: 50rem;
    display: flex;
    align-content: center;
    justify-content: center; }
    .footer_right .bottom_button:hover {
      cursor: pointer;
      text-decoration: none !important;
      background-color: #39d3db;
      border: solid #39d3db 0px; }

