            HEADER
==============================*/
header {
  background: transparent !important;
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 8rem;
  color: #ffffff; }

.header_container {
  background: rgba(0, 0, 0, 0) !important;
  width: 80%;
  margin: 0 auto;
  height: 8rem;
  color: #ffffff;
  display: block;
  position: fixed;
  top: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media only screen and (max-width: 56.25em) {
    .header_container {
      display: flex;
      flex-direction: column;
      height: 20rem; } }
  .header_container nav {
    margin: 0 auto; }
    .header_container nav .navbar {
      display: flex !important;
      justify-content: center; }
      @media only screen and (max-width: 56.25em) {
        .header_container nav .navbar {
          width: 100%;
          margin: 0;
          height: 10rem; } }
    .header_container nav a {
      margin: 0 1rem;
      color: #ffffff !important;
      font-size: 1.4rem; }
      .header_container nav a:hover {
        color: #39d3db !important; }
    .header_container nav .nav-link:hover {
      color: #39d3db; }
  .header_container .language_selector_container {
    background-color: transparent;
    width: 8rem;
    height: 100%;
    align-self: center;
    font-size: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center; }
    .header_container .language_selector_container svg {
      font-size: 2.5rem;
      margin-right: 1rem; }
    .header_container .language_selector_container .Dropdown-root {
      font-size: 1rem; }
      .header_container .language_selector_container .Dropdown-root .Dropdown-control {
        border: 1px solid white;
        border-radius: 4px;
        background: transparent;
        color: white; }
      .header_container .language_selector_container .Dropdown-root .Dropdown-menu {
        background: transparent; }
      .header_container .language_selector_container .Dropdown-root .Dropdown-option {
        color: white;
        background: transparent; }
      .header_container .language_selector_container .Dropdown-root .Dropdown-option.is-selected {
        color: #39d3db; }

.dropdown-menu a {
  color: black !important; }

.dropdown-item {
  margin: 0 !important;
  background-color: rgba(0, 0, 0, 0); }
  .dropdown-item:active {
    color: black !important; }

.graphical_container {
